"use strict";
module.exports = {
    postList: {},
    pages: {},
    api: {},
    categories: [],
    index: 0,
    generateKey: function (obj, url) {
        var key;
        var generatedKey = url + this.index;
        for (key in obj) {
            generatedKey += obj[key];
        }
        this.index += 1;
        return generatedKey;
    }
};



